@font-face {
  font-family: "Inter";
  src: url(./fonts/Inter-VariableFont_opsz\,wght.ttf);
}
@font-face{
  font-family: "Karla";
  src: url("./fonts/Karla-VariableFont_wght.ttf");
}
html, body{
  width: 100vw;
  margin: 0;
  padding: 0;
  height: 100%;
}
#root{
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: rgba(11, 36, 52, 1);
  display: flex;
  justify-content: center;
  align-items: center;
}

main{
  width: 300px;
  height: 300px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  padding: 34px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.game-info{
  text-align: center;
}
.game-name{
  color: #2B283A;
  font-family: "Karla";
  font-size: 26px;
  font-weight: 700;
}
.game-rules{
  color: rgba(74, 78, 116, 1);
  font-family: "Inter";
  font-size: 13px;
  font-weight: 400;
}

.field{
  display: grid;
  grid-template-columns: repeat(5, 15%);
  grid-template-rows: auto auto;
  justify-content: space-between;
  row-gap: 19px;
  width: 100%;
}
.diecell{
  box-sizing: border-box;
  text-align: center;
  font-weight: bold;
  width: 36px;
  height: 36px;
  padding: 6px 0 6px;
  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 2px 1px 0px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  font-family: "Karla";
  font-weight: 900;
}
.diecell.selected{
  background-color: rgba(89, 227, 145, 1);
}

.btn-roll{
  border: none;
  background-color: rgba(80, 53, 255, 1);
  color: #FFF;
  border-radius: 4px;
  width: 92px;
height: 35px;
text-align: center;
font-family: "Karla";
font-size: 16px;
font-weight: 700;
}